<template>
    <layout title="Clientes">
        <h1 class="mb-8 font-bold text-3xl">Clientes</h1>
        <div class="mb-6 flex justify-between items-center">
            <search-filter v-model="form.search" class="w-full max-w-sm mr-4" @reset="reset">
                <label class="block text-grey-darkest">Basura:</label>
                <select v-model="form.trashed" class="mt-1 w-full form-select">
                    <option :value="null" />
                    <option value="with">Incluir borrados</option>
                    <option value="only">Solo borrados</option>
                </select>
            </search-filter>
            <inertia-link class="btn-green" :class="{'hidden':clients.data.length === 0}" :href="route('invoice.clients.create', organization.slug)">
                <span>Crear</span>
                <span class="hidden md:inline">Cliente</span>
            </inertia-link>
        </div>
        <div class="bg-white rounded shadow overflow-x-auto">
            <table class="w-full whitespace-no-wrap">
                <tr class="text-left font-bold">
                    <th class="px-6 pt-6 pb-4">Nombre</th>
                    <th class="px-6 pt-6 pb-4">Cedula</th>
                    <th class="px-6 pt-6 pb-4" colspan="2">Telefono</th>
                </tr>
                <tr v-for="client in clients.data" :key="client.id" class="hover:bg-grey-lightest focus-within:bg-grey-lightest">
                    <td class="border-t">
                        <inertia-link class="px-6 py-4 flex items-center focus:text-green" :href="route('invoice.clients.edit', {'slug':organization.slug, 'client':client.id})">
                            <div class="flex flex-col">
                               <div>
                                   {{ client.name }} {{ client.last_name}}
                                   <icon v-if="client.deleted_at" name="trash" class="flex-no-shrink w-3 h-3 fill-grey ml-2" />
                               </div>
                                <small class="pt-2 text-grey-dark">Debe: ${{ client.bills }}</small>
                           </div>
                        </inertia-link>
                    </td>
                    <td class="border-t">
                        <inertia-link class="px-6 py-4 flex items-center" :href="route('invoice.clients.edit', {'slug':organization.slug, 'client':client.id})" tabindex="-1">
                            {{ client.id_card }}
                        </inertia-link>
                    </td>
                    <td class="border-t">
                        <inertia-link class="px-6 py-4 flex items-center" :href="route('invoice.clients.edit', {'slug':organization.slug, 'client':client.id})" tabindex="-1">
                            {{ client.phone }}
                        </inertia-link>
                    </td>
                    <td class="border-t w-px">
                        <inertia-link class="px-4 flex items-center" :href="route('invoice.clients.edit', {'slug':organization.slug, 'client':client.id})" tabindex="-1">
                            <icon name="cheveron-right" class="block w-6 h-6 fill-grey" />
                        </inertia-link>
                    </td>
                </tr>
                <tr v-if="clients.data.length === 0" class="">
                    <td class="border-t px-6 py-4" colspan="3">
